         <div class=\"big-title \">{{ selectedCardContent.titleRest }} </div>\n                </div>\n                <div class=\"long-text\"> {{ selectedCardContent?.desc }}</div>\n                <div class=\"buttons-container\">\n                    <a href=\"#\" class=\"start-btn\">GET STARTED <i\n                            class=\"mdi mdi-arrow-right arrow-icon\"></i></a>\n                </div>\n            </div>\n        </div>\n        <div class=\"right-section\">\n            <img class=\"get-started-img\" ref=\"contentCard\" :src=\"selectedCardContent.imgPath\" alt=\"\">\n            <div class=\"gradient\"></div>\n        </div>\n    </div>\n</template>\n\n<script setup lang=\"ts\">\nimport { HtmlHTMLAttributes, ref, onMounted } from 'vue';\nimport darkIcon1 from './Images/IconsDark1.svg';\nimport darkIcon2 from './Images/IconsDark2.svg';\nimport darkIcon3 from './Images/IconsDark3.svg';\nimport darkIcon4 from './Images/IconsDark4.svg';\nimport darkIcon5 from './Images/IconsDark5.svg';\n\nimport lightIcon1 from './Images/IconsLight1.svg';\nimport lightIcon2 from './Images/IconsLight2.svg';\nimport lightIcon3 from './Images/IconsLight3.svg';\nimport lightIcon4 from './Images/IconsLight4.svg';\nimport lightIcon5 from './Images/IconsLight5.svg';\n\nimport create from './Images/create.svg';\nimport build from './Images/build.svg';\nimport crud from './Images/crud.svg';\nimport publish from './Images/publish.svg';\nimport workflow from './Images/workflow.svg';\n\nconst contentCard = ref();\ninterface ICard {\n    cardImage: string,\n    cardText: string,\n    degree: number,\n    link: string,\n}\n\nconst defaultContent = {\n    coloredTitle: \"Create\",\n    titleRest: \"Stunning UI Pages\",\n    desc: \"Design appealing layouts, interactive elements, and seamless user experiences on any device with our powerful UI development tools.\",\n    imgPath: create\n};\n\nconst clickedCardIndex = ref<number | null>(0);\nconst selectedCardContent = ref<{ coloredTitle: string, titleRest: string, desc: string, imgPath: string }>(defaultContent);\n\nconst darkIconArray = [darkIcon1, darkIcon2, darkIcon3, darkIcon4, darkIcon5];\nconst lightIconArray = [lightIcon1, lightIcon2, lightIcon3, lightIcon4, lightIcon5];\n\nconst cardData = ref<ICard[]>([\n    {\n        cardImage: lightIconArray[0],\n        cardText: \"UI Design\",\n        degree: 160,\n        link: \"https://docs.onplateau.com/Develop-App-With-Plateau-Studio/creating-an-application#how-to-create-an-application\"\n    },\n    {\n        cardImage: lightIconArray[1],\n        cardText: \"Native Mobile Applications\",\n        degree: 0,\n        link: \"https://docs.onplateau.com/Release-Notes/Plateau%20Studio%20V1.9.0#plateau-mobile\"\n    },\n    {\n        cardImage: lightIconArray[2],\n        cardText: \"Workflow Development\",\n        degree: -100,\n        link: \"https://docs.onplateau.com/Workflows/creating-workflow\"\n    },\n    {\n        cardImage: lightIconArray[3],\n        cardText: \"CRUD Service\",\n        degree: -220,\n        link: \"https://docs.onplateau.com/Introduction/Feature-Set/studio-feature-set\"\n\n    },\n    {\n        cardImage: lightIconArray[4],\n        cardText: \"Publish on Azure & More\",\n        degree: -320,\n        link: \"https://docs.onplateau.com/Publish/using-publish\"\n    },\n]);\n\nconst handleCardClick = (index: number) => {\n    clickedCardIndex.value = index;\n    const currentCard = cardData.value[index].cardText;\n    cardData.value.forEach((card, i) => {\n        card.cardImage = i === index ? darkIconArray[i] : lightIconArray[i];\n    });\n    const currentDegree = cardData.value[index].degree;\n    const pageLink = cardData.value[index].link;\n    const cardContents = getContent(currentCard);\n    selectedCardContent.value = cardContents;\n    contentCard.value.style.background = \"linear-gradient(to right, #F5F5FA, #F5F5FA), linear-gradient(to right, #F295B3, #C1A5D5, #6785D2 70%, #F5F5FA 30%);\"\n    const getGradient = document.getElementsByClassName(\"gradient\")[0];\n    const getLink = document.getElementsByClassName(\"start-btn\")[1];\n    if (getGradient instanceof HTMLElement) {\n        getGradient.style.transform = `rotate(${currentDegree}deg)`;\n    }\n\n    if (getLink instanceof HTMLAnchorElement) {\n        getLink.href = pageLink;\n        getLink.target = \"_blank\"\n    }\n\n};\n// const redirectLink = () => {\n//     const getLink = document.getElementsByClassName(\"start-btn\")[1].href;\n//     window.location.href= getLink;\n// }\nconst getContent = (cardText: string) => {\n    switch (cardText) {\n        case \"UI Design\":\n            return {\n                coloredTitle: \"Create\",\n                titleRest: \"Stunning UI Pages\",\n                desc: \"Design appealing layouts, interactive elements, and seamless user experiences on any device with our powerful UI development tools.\",\n                imgPath: create\n            };\n        case \"Native Mobile Applications\":\n            return {\n                coloredTitle: \"Build\",\n                titleRest: \"Native Mobile Apps\",\n                desc: \"Extend your reach to mobile devices with our mobile development capabilities. Build native or cross-platform mobile apps for exceptional user experiences on iOS and Android.\",\n                imgPath: build\n            };\n        case \"Workflow Development\":\n            return {\n                coloredTitle: \"Workflow\",\n                titleRest: \"Development\",\n                desc: \"Build custom workflows, automate tasks, and enhance collaboration. Increase productivity with a flexible workflow management system.\",\n                imgPath: workflow\n            };\n        case \"CRUD Service\":\n            return {\n                coloredTitle: \"CRUD\",\n                titleRest: \"Service Development\",\n                desc: \"Simplify data management and service integration with Plateau CRUD Entity/Service development. Easily create, read, update, and delete data entities, and seamlessly connect with external services to power your applications.\",\n                imgPath: crud\n            };\n        case \"Publish on Azure & More\":\n            return {\n                coloredTitle: \"Publish Apps\",\n                titleRest: \"To Azure & StudioApps\",\n                desc: \"Publish your web apps to Azure Cloud with one click and showcase mobile apps via StudioApps. Plateau apps can be published to Google Cloud Platform Playground. Need an on-pre installation? Export your package and set up your application.\",\n                imgPath: publish\n            };\n        default:\n            return { coloredTitle: \"\", titleRest: \"\", desc: \"\", imgPath: \"\" };\n    }\n};\nonMounted(() => {\n    handleCardClick(0);\n});\n</script>\n\n<style scoped>\n.wrapper-container {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    gap: 25px;\n    padding: 0 100px;\n}\n\n.decoration {\n    height: 15px;\n    background-color: #D0D1E9;\n    margin-bottom: -3%\n}\n\n.card-images {\n    width: 119px;\n    height: 118px;\n}\n\n.card-text {\n    font-weight: 600;\n    font-family: Roboto;\n    font-size: 24px;\n    line-height: 29px;\n    color: #151233;\n    text-align: center;\n}\n\n.card {\n    width: 220px;\n    height: 300px;\n    border-radius: 32px;\n    border: 2px solid #D0D1E9;\n    padding: 46px 20px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    gap: 32px;\n}\n\n.card:hover {\n    cursor: pointer;\n}\n\n.card:hover:not(.active-card) {\n    border: 4px solid #6785D2;\n    cursor: pointer;\n}\n\n.active-card {\n    border: 4px solid transparent;\n    border-radius: 32px;\n    background:\n        linear-gradient(to right, white, white),\n        linear-gradient(to right, #F295B3, #C1A5D5, #6785D2);\n    background-clip: padding-box, border-box;\n    background-origin: padding-box, border-box;\n    box-shadow: 0px 8px 32px 0px #F295B359;\n}\n\n.content-container {\n    display: flex;\n    margin: 0px 150px\n}\n\n.start-btn {\n    width: 221px;\n    height: 56px;\n    border-radius: 4px;\n    font-size: 18px;\n    font-weight: 600;\n    line-height: 24px;\n    text-decoration: none;\n    cursor: pointer;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    margin-top: 65px;\n    gap: 16px;\n    color: #151233;\n    border: 1px solid #151233;\n    background-color: transparent;\n    transition: 0.2s ease;\n\n}\n\n.start-btn:hover {\n    border: 1px solid #6785D2;\n    color: #6785D2;\n    transition: 0.2s ease;\n}\n\n.arrow-icon {\n    font-size: 24px;\n}\n\n.right-section {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    position: relative;\n}\n\n.left-section {\n    display: flex;\n    width: 100%;\n}\n\n.left-section-container {\n    margin: 61px 27px 61px 0;\n    width: 650px;\n}\n\n\n.big-title {\n    color: #171717;\n    font-weight: 900;\n    font-size: 62px;\n    font-family: Roboto;\n}\n\n.app-dev {\n    background: linear-gradient(123.04deg, #F295B3 17.11%, #C1A5D5 53.96%, #6785D2 95.33%);\n    -webkit-background-clip: text;\n    -webkit-text-fill-color: transparent;\n    /* animation: gradientAnimation 10s infinite ease-in; */\n}\n\n.long-text {\n    font-weight: 300;\n    font-size: 20px;\n    line-height: 30px;\n    color: #8B8B8B;\n    font-family: Roboto;\n}\n\n.big-title-container {\n    margin: 20px 0;\n}\n\n.get-started-img {\n    width: 400px;\n    height: 400px;\n    border-radius: 205px;\n}\n\n.gradient {\n    border: 20px solid transparent;\n    border-radius: 50%;\n    width: 440px;\n    position: absolute;\n    /* right: 420px; */\n    height: 440px;\n    /* background:\n        linear-gradient(to right, #F5F5FA, #F5F5FA),\n        linear-gradient(to left, #F295B3, #C1A5D5, #6785D2); */\n    background:\n        linear-gradient(to right, #F5F5FA, #F5F5FA),\n        linear-gradient(to right, #F295B3, #C1A5D5, #6785D2 50%, #F5F5FA 50%);\n    background-clip: padding-box, border-box;\n    background-origin: padding-box, border-box;\n    /* transform: rotate(45deg); */\n    transition: 1s ease;\n    top: 79;\n    z-index: -1;\n}\n</style>\n"],"sourceRoot":""}]);
